export default {
  data() {
    return {
      navItems: [],
    };
  },
  props: {
    banner: {
      type: String,
      default: "/banner.png",
    },
    title: {
      type: String,
      required: true,
    },
  },
  async fetch() {
    const data = await this.$axios.$get(`/data/data.json`, {
      params: {
        populate: ["image"],
      },
    });
    this.navItems = data.data;
  },
};
